<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="auditStudio">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="addStudio">新增工作室</el-button>
            </div>
            <div>
                <el-input
                    placeholder="请输入手机号\申请人"
                    size="small"
                    style="width: 220px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="请选择状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="">查询</el-button>
                <el-button type="warning" size="small" @click="">重置</el-button>
            </div>
        </ToolBar>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="date"
                label="ID"
                width="180">
            </el-table-column>
            <el-table-column
                prop="name"
                label="工作室名称"
                width="180">
            </el-table-column>
            <el-table-column
                prop="address"
                label="申请人">
            </el-table-column>
            <el-table-column
                prop="address"
                label="注册手机号">
            </el-table-column>
            <el-table-column
                prop="address"
                label="省">
            </el-table-column>
            <el-table-column
                prop="address"
                label="市">
            </el-table-column>
            <el-table-column
                prop="address"
                label="区/街道">
            </el-table-column>
            <el-table-column
                prop="address"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建时间">
            </el-table-column>
            <el-table-column
                prop="address"
                label="操作人">
            </el-table-column>
            <el-table-column
                label="操作"
                width="78">
                <template slot-scope="scope">
                    <el-button @click="auditeStatus(scope.row)" type="primary" size="small"
                    >审核
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            addStudio() {
              this.$router.push('/studio_add_3')
            },
            auditeStatus() {
                this.$router.push('/status_audite_3')
            },
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {
                    }
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
